
















import { defineComponent, PropType } from '@vue/composition-api';

interface Breadcrumb {
	to: string;
	name: string;
	disabled?: boolean;
}

export default defineComponent({
	props: {
		items: {
			type: Array as PropType<Breadcrumb[]>,
			default: () => [],
		},
	},
	setup() {
		return {};
	},
});
