






















import { defineComponent, PropType, computed } from '@vue/composition-api';

type User = {
	id: number;
	avatar: {
		data: {
			thumbnails: {
				key: string;
				url: string;
			}[];
		};
	};
	first_name: string;
	last_name: string;
};

export default defineComponent({
	props: {
		value: {
			type: Object as PropType<User>,
			default: null,
		},
		display: {
			type: String as PropType<'avatar' | 'name'>,
			default: 'avatar',
		},
		circle: {
			type: Boolean,
			default: false,
		},
	},
	setup(props) {
		const src = computed(() => {
			if (props.value === null) return null;
			return (
				props.value?.avatar?.data?.thumbnails?.find((thumb) => thumb.key === 'directus-small-crop')?.url || null
			);
		});

		return { src };
	},
});
