































import { defineComponent, ref, watch, onUnmounted, computed } from '@vue/composition-api';
import api from '@/api';
import useProjectsStore from '@/stores/projects';

type User = {
	first_name: string;
	last_name: string;
	email: string;
	avatar: {
		data: {
			thumbnails: any[];
		};
	};
};

export default defineComponent({
	props: {
		user: {
			type: Number,
			required: true,
		},
	},
	setup(props) {
		const projectsStore = useProjectsStore();

		const loading = ref(false);
		const error = ref(null);
		const data = ref<User>(null);

		const avatarSrc = computed(() => {
			if (data.value === null) return null;

			return data.value.avatar?.data?.thumbnails?.find((thumbnail) => thumbnail.key === 'directus-medium-crop')
				?.url;
		});

		const active = ref(false);

		watch(active, () => {
			if (active.value === true && data.value === null && loading.value === false) {
				fetchUser();
			}
		});

		onUnmounted(() => {
			loading.value = false;
			error.value = null;
			data.value = null;
		});

		return { loading, error, data, active, avatarSrc };

		async function fetchUser() {
			loading.value = true;
			error.value = null;
			const { currentProjectKey } = projectsStore.state;

			try {
				const response = await api.get(`/${currentProjectKey}/users/${props.user}`, {
					params: {
						fields: ['first_name', 'last_name', 'avatar.data', 'role.name', 'status', 'email'],
					},
				});
				data.value = response.data.data;
			} catch (err) {
				error.value = err;
			} finally {
				loading.value = false;
			}
		}
	},
});
