











import { defineComponent, PropType } from '@vue/composition-api';

export default defineComponent({
	props: {
		icon: {
			type: String,
			default: 'box',
		},
		title: {
			type: String,
			required: true,
		},
		type: {
			type: String as PropType<'info' | 'success' | 'warning' | 'danger'>,
			default: 'info',
		},
		center: {
			type: Boolean,
			default: false,
		},
	},
});
