























































































































import { defineComponent, computed, toRefs, ref } from '@vue/composition-api';
import useProjectsStore from '@/stores/projects';
import FilesNavigation from '../../components/navigation/';
import { i18n } from '@/lang';
import router from '@/router';
import RevisionsDrawerDetail from '@/views/private/components/revisions-drawer-detail';
import CommentsDrawerDetail from '@/views/private/components/comments-drawer-detail';
import useItem from '@/composables/use-item';
import SaveOptions from '@/views/private/components/save-options';
import FilePreview from '@/views/private/components/file-preview';
import ImageEditor from '@/views/private/components/image-editor';
import { nanoid } from 'nanoid';
import FileLightbox from '@/views/private/components/file-lightbox';
import useFieldsStore from '@/stores/fields';
import { Field } from '@/stores/fields/types';
import FileInfoDrawerDetail from './components/file-info-drawer-detail.vue';
import marked from 'marked';

type Values = {
	[field: string]: any;
};

export default defineComponent({
	name: 'files-detail',
	beforeRouteLeave(to, from, next) {
		const self = this as any;
		const hasEdits = Object.keys(self.edits).length > 0;

		if (hasEdits) {
			self.confirmLeave = true;
			self.leaveTo = to.fullPath;
			return next(false);
		}

		return next();
	},
	components: {
		FilesNavigation,
		RevisionsDrawerDetail,
		CommentsDrawerDetail,
		SaveOptions,
		FilePreview,
		ImageEditor,
		FileLightbox,
		FileInfoDrawerDetail,
	},
	props: {
		primaryKey: {
			type: String,
			required: true,
		},
	},
	setup(props) {
		const projectsStore = useProjectsStore();
		const { currentProjectKey } = toRefs(projectsStore.state);
		const { primaryKey } = toRefs(props);
		const { breadcrumb } = useBreadcrumb();
		const fieldsStore = useFieldsStore();

		const revisionsDrawerDetail = ref<Vue>(null);

		const { isNew, edits, item, saving, loading, error, save, remove, deleting, saveAsCopy, isBatch } = useItem(
			ref('directus_files'),
			primaryKey
		);

		const hasEdits = computed<boolean>(() => Object.keys(edits.value).length > 0);
		const confirmDelete = ref(false);
		const cacheBuster = ref(nanoid());
		const editActive = ref(false);
		const previewActive = ref(false);

		// These are the fields that will be prevented from showing up in the form
		const fieldsBlacklist: string[] = ['type', 'width', 'height', 'filesize', 'checksum'];

		const formFields = computed(() => {
			return fieldsStore
				.getFieldsForCollection('directus_files')
				.filter((field: Field) => fieldsBlacklist.includes(field.field) === false);
		});

		const confirmLeave = ref(false);
		const leaveTo = ref<string>(null);

		return {
			item,
			loading,
			error,
			isNew,
			breadcrumb,
			edits,
			hasEdits,
			saving,
			saveAndQuit,
			deleteAndQuit,
			confirmDelete,
			deleting,
			saveAndStay,
			saveAndAddNew,
			saveAsCopyAndNavigate,
			isBatch,
			changeCacheBuster,
			cacheBuster,
			editActive,
			previewActive,
			revisionsDrawerDetail,
			formFields,
			marked,
			confirmLeave,
			leaveTo,
			discardAndLeave,
		};

		function changeCacheBuster() {
			cacheBuster.value = nanoid();
		}

		function useBreadcrumb() {
			const breadcrumb = computed(() => [
				{
					name: i18n.t('file_library'),
					to: `/${currentProjectKey.value}/files/`,
				},
			]);

			return { breadcrumb };
		}

		async function saveAndQuit() {
			await save();
			router.push(`/${currentProjectKey.value}/files`);
		}

		async function saveAndStay() {
			const savedItem: Record<string, any> = await save();

			revisionsDrawerDetail.value?.$data?.refresh?.();

			if (props.primaryKey === '+') {
				// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
				const newPrimaryKey = savedItem.id;
				router.replace(`/${currentProjectKey.value}/collections/files/${newPrimaryKey}`);
			}
		}

		async function saveAndAddNew() {
			await save();
			router.push(`/${currentProjectKey.value}/files/+`);
		}

		async function saveAsCopyAndNavigate() {
			const newPrimaryKey = await saveAsCopy();
			router.push(`/${currentProjectKey.value}/files/${newPrimaryKey}`);
		}

		async function deleteAndQuit() {
			await remove();
			router.push(`/${currentProjectKey.value}/files`);
		}

		function discardAndLeave() {
			if (!leaveTo.value) return;
			edits.value = {};
			router.push(leaveTo.value);
		}
	},
});
