









































import { defineComponent, ref, computed, provide } from '@vue/composition-api';

export default defineComponent({
	model: {
		prop: 'active',
		event: 'toggle',
	},
	props: {
		title: {
			type: String,
			required: true,
		},
		subtitle: {
			type: String,
			default: null,
		},
		active: {
			type: Boolean,
			default: undefined,
		},
		persistent: {
			type: Boolean,
			default: false,
		},
		noPadding: {
			type: Boolean,
			default: false,
		},
		formWidth: {
			// If the modal is used to just render a form, it needs to be a little smaller to
			// allow the form to be rendered in it's correct full size
			type: Boolean,
			default: false,
		},
		heading: {
			type: String,
			default: false,
		},
		subheading: {
			type: String,
			default: null,
		},
	},
	setup(props, { emit }) {
		const sidebarActive = ref(false);
		const localActive = ref(false);

		const mainEl = ref<Element>();

		provide('main-element', mainEl);

		const _active = computed({
			get() {
				return props.active === undefined ? localActive.value : props.active;
			},
			set(newActive: boolean) {
				localActive.value = newActive;
				emit('toggle', newActive);
			},
		});

		return { sidebarActive, _active, mainEl };
	},
});
