































import { defineComponent, computed, ref } from '@vue/composition-api';
import readableMimeType from '@/utils/readable-mime-type';
import bytes from 'bytes';
import i18n from '@/lang';
import localizedFormat from '@/utils/localized-format';

export default defineComponent({
	inheritAttrs: false,
	props: {
		type: {
			type: String,
			default: null,
		},
		width: {
			type: Number,
			default: null,
		},
		height: {
			type: Number,
			default: null,
		},
		filesize: {
			type: Number,
			default: null,
		},
		uploaded_on: {
			type: String,
			default: null,
		},
		checksum: {
			type: String,
			default: null,
		},
	},
	setup(props) {
		const size = computed(() => {
			if (!props.filesize) return null;

			return bytes(props.filesize, { decimalPlaces: 2, unitSeparator: ' ' }); // { locale: i18n.locale.split('-')[0] }
		});

		const creationDate = ref<string>(null);

		localizedFormat(new Date(props.uploaded_on), String(i18n.t('date-fns_datetime'))).then((result) => {
			creationDate.value = result;
		});

		return { readableMimeType, size, creationDate };
	},
});
