





























import { defineComponent, ref } from '@vue/composition-api';

export default defineComponent({
	model: {
		prop: 'active',
		event: 'toggle',
	},
	props: {
		active: {
			type: Boolean,
			default: false,
		},
		saving: {
			type: Boolean,
			default: false,
		},
	},
	setup(props, { emit }) {
		const bookmarkName = ref(null);

		return { bookmarkName, cancel };

		function cancel() {
			bookmarkName.value = null;
			emit('toggle', false);
		}
	},
});
