





























import { defineComponent, ref, watch } from '@vue/composition-api';

export default defineComponent({
	model: {
		prop: 'active',
		event: 'toggle',
	},
	props: {
		active: {
			type: Boolean,
			default: false,
		},
		saving: {
			type: Boolean,
			default: false,
		},
		name: {
			type: String,
			required: true,
		},
	},
	setup(props, { emit }) {
		const bookmarkName = ref(props.name);

		watch(
			() => props.name,
			(newName: string) => (bookmarkName.value = newName)
		);

		return { bookmarkName, cancel };

		function cancel() {
			emit('toggle', false);
		}
	},
});
