






































































































































































import { defineComponent, computed, toRefs, ref } from '@vue/composition-api';
import useProjectsStore from '@/stores/projects';
import CollectionsNavigation from '../../components/navigation/';
import router from '@/router';
import CollectionsNotFound from '../not-found/';
import useCollection from '@/composables/use-collection';
import RevisionsDrawerDetail from '@/views/private/components/revisions-drawer-detail';
import CommentsDrawerDetail from '@/views/private/components/comments-drawer-detail';
import useItem from '@/composables/use-item';
import SaveOptions from '@/views/private/components/save-options';
import i18n from '@/lang';
import marked from 'marked';
import useShortcut from '@/composables/use-shortcut';

type Values = {
	[field: string]: any;
};

export default defineComponent({
	name: 'collections-detail',
	beforeRouteLeave(to, from, next) {
		const self = this as any;
		const hasEdits = Object.keys(self.edits).length > 0;

		if (hasEdits) {
			self.confirmLeave = true;
			self.leaveTo = to.fullPath;
			return next(false);
		}

		return next();
	},
	components: {
		CollectionsNavigation,
		CollectionsNotFound,
		RevisionsDrawerDetail,
		CommentsDrawerDetail,
		SaveOptions,
	},
	props: {
		collection: {
			type: String,
			required: true,
		},
		primaryKey: {
			type: String,
			required: true,
		},
	},
	setup(props) {
		const projectsStore = useProjectsStore();
		const { currentProjectKey } = toRefs(projectsStore.state);
		const { collection, primaryKey } = toRefs(props);
		const { breadcrumb } = useBreadcrumb();

		const revisionsDrawerDetail = ref<Vue>(null);

		const { info: collectionInfo, softDeleteStatus, primaryKeyField } = useCollection(collection);

		const {
			isNew,
			edits,
			item,
			saving,
			loading,
			error,
			save,
			remove,
			deleting,
			softDeleting,
			saveAsCopy,
			isBatch,
			refresh,
		} = useItem(collection, primaryKey);

		const hasEdits = computed<boolean>(() => Object.keys(edits.value).length > 0);

		const confirmDelete = ref(false);
		const confirmSoftDelete = ref(false);

		const confirmLeave = ref(false);
		const leaveTo = ref<string>(null);

		const backLink = computed(() => `/${currentProjectKey.value}/collections/${collection.value}/`);

		const templateValues = computed(() => {
			return {
				...(item.value || {}),
				...edits.value,
			};
		});

		const title = computed(() => {
			if (isBatch.value) {
				const itemCount = props.primaryKey.split(',').length;
				return i18n.t('editing_in_batch', { count: itemCount });
			}

			return isNew.value
				? i18n.t('adding_in', { collection: collectionInfo.value?.name })
				: i18n.t('editing_in', { collection: collectionInfo.value?.name });
		});

		useShortcut('mod+s', saveAndStay);
		useShortcut('mod+shift+s', saveAndAddNew);

		return {
			item,
			loading,
			backLink,
			error,
			isNew,
			edits,
			hasEdits,
			saving,
			collectionInfo,
			saveAndQuit,
			deleteAndQuit,
			confirmDelete,
			confirmSoftDelete,
			deleting,
			softDeleting,
			saveAndStay,
			saveAndAddNew,
			saveAsCopyAndNavigate,
			isBatch,
			softDeleteStatus,
			templateValues,
			breadcrumb,
			title,
			revisionsDrawerDetail,
			marked,
			refresh,
			confirmLeave,
			leaveTo,
			discardAndLeave,
		};

		function useBreadcrumb() {
			const breadcrumb = computed(() => [
				{
					name: collectionInfo.value?.name,
					to: `/${currentProjectKey.value}/collections/${props.collection}`,
				},
			]);

			return { breadcrumb };
		}

		async function saveAndQuit() {
			await save();
			router.push(`/${currentProjectKey.value}/collections/${props.collection}`);
		}

		async function saveAndStay() {
			const savedItem: Record<string, any> = await save();

			revisionsDrawerDetail.value?.$data?.refresh?.();

			if (props.primaryKey === '+') {
				// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
				const newPrimaryKey = savedItem[primaryKeyField.value!.field];
				router.replace(`/${currentProjectKey.value}/collections/${props.collection}/${newPrimaryKey}`);
			}
		}

		async function saveAndAddNew() {
			await save();
			router.push(`/${currentProjectKey.value}/collections/${props.collection}/+`);
		}

		async function saveAsCopyAndNavigate() {
			const newPrimaryKey = await saveAsCopy();
			router.push(`/${currentProjectKey.value}/collections/${props.collection}/${newPrimaryKey}`);
		}

		async function deleteAndQuit(soft = false) {
			await remove(soft);
			router.push(`/${currentProjectKey.value}/collections/${props.collection}`);
		}

		function discardAndLeave() {
			if (!leaveTo.value) return;
			edits.value = {};
			router.push(leaveTo.value);
		}
	},
});
