





























import { defineComponent, PropType, computed, ref } from '@vue/composition-api';
import i18n from '@/lang';
import { Permission } from '../../composables/use-permissions';

export default defineComponent({
	props: {
		value: {
			type: String,
			required: true,
		},
		options: {
			type: Array as PropType<string[]>,
			required: true,
		},
		loading: {
			type: Boolean,
			default: false,
		},
		status: {
			type: String,
			default: null,
		},
		savePermission: {
			type: Function,
			required: true,
		},
		type: {
			type: String,
			required: true,
		},
		permissionId: {
			type: Number,
			default: undefined,
		},
		collection: {
			type: String,
			required: true,
		},
		role: {
			type: Number,
			required: true,
		},
	},
	setup(props) {
		const iconName = computed(() => {
			return getIconForValue(props.value);
		});

		const _options = computed(() => {
			return props.options.map((option) => ({
				value: option,
				name: i18n.t(option),
				icon: getIconForValue(option),
			}));
		});

		const saving = ref(false);

		return { iconName, _options, save, saving };

		async function save(newValue: string) {
			saving.value = true;

			const values: Partial<Permission> = {
				[props.type]: newValue,
				collection: props.collection,
				status: props.status,
				role: props.role,
			};

			if (props.permissionId) {
				values.id = props.permissionId;
			}

			await props.savePermission(values);

			saving.value = false;
		}

		function getIconForValue(value: string) {
			switch (value) {
				case 'indeterminate':
					return 'remove';
				case 'mine':
					return 'person';
				case 'role':
					return 'group';
				case 'full':
					return 'check';
				case 'read':
					return 'remove_red_eye';
				case 'create':
					return 'add';
				case 'update':
					return 'edit';
				case 'none':
					return null;
				default:
					return 'check_box_outline_blank';
			}
		}
	},
});
