








































































import { defineComponent, computed, toRefs, ref } from '@vue/composition-api';
import SettingsNavigation from '../../../components/navigation/';
import useCollection from '@/composables/use-collection/';
import FieldsManagement from './components/fields-management';
import useProjectsStore from '@/stores/projects';
import useItem from '@/composables/use-item';
import router from '@/router';
import useCollectionsStore from '@/stores/collections';
import marked from 'marked';

export default defineComponent({
	components: { SettingsNavigation, FieldsManagement },
	props: {
		collection: {
			type: String,
			required: true,
		},
	},
	setup(props) {
		const projectsStore = useProjectsStore();
		const { collection } = toRefs(props);
		const { info: collectionInfo, fields } = useCollection(collection);
		const { currentProjectKey } = toRefs(projectsStore.state);
		const collectionsStore = useCollectionsStore();

		const { isNew, edits, item, saving, loading, error, save, remove, deleting, saveAsCopy, isBatch } = useItem(
			ref('directus_collections'),
			collection
		);

		const hasEdits = computed<boolean>(() => Object.keys(edits.value).length > 0);

		const confirmDelete = ref(false);

		return {
			collectionInfo,
			fields,
			confirmDelete,
			isNew,
			edits,
			item,
			saving,
			loading,
			error,
			save,
			remove,
			deleting,
			saveAsCopy,
			isBatch,
			deleteAndQuit,
			saveAndQuit,
			hasEdits,
			marked,
		};

		async function deleteAndQuit() {
			await remove();
			router.push(`/${currentProjectKey.value}/settings/data-model`);
		}

		async function saveAndQuit() {
			await save();
			await collectionsStore.hydrate();
			router.push(`/${currentProjectKey.value}/settings/data-model`);
		}
	},
});
