























import { defineComponent, computed } from '@vue/composition-api';

export default defineComponent({
	props: {
		disabled: {
			type: Boolean,
			default: false,
		},
		autofocus: {
			type: Boolean,
			default: false,
		},
		fullWidth: {
			type: Boolean,
			default: true,
		},
		value: {
			type: String,
			default: null,
		},
		expandOnFocus: {
			type: Boolean,
			default: false,
		},
		placeholder: {
			type: String,
			default: null,
		},
		trim: {
			type: Boolean,
			default: true,
		},
	},
	setup(props, { emit, listeners }) {
		const _listeners = computed(() => ({
			...listeners,
			input: emitValue,
		}));

		const hasContent = computed(() => props.value && props.value.length > 0);

		return { _listeners, hasContent };

		function emitValue(event: InputEvent) {
			let value = (event.target as HTMLInputElement).value;
			if (props.trim === true) {
				value = value.trim();
			}
			emit('input', value);
		}
	},
});
