




















































import { defineComponent, ref, watch, computed, reactive, PropType } from '@vue/composition-api';
import formatLocalized from '@/utils/localized-format';
import { i18n } from '@/lang';
import parse from 'date-fns/parse';
import format from 'date-fns/format';
import parseISO from 'date-fns/parseISO';

type LocalValue = {
	month: null | number;
	date: null | number;
	year: null | number;
	hours: null | number;
	minutes: null | number;
	seconds: null | number;
	period: 'am' | 'pm';
};

export default defineComponent({
	props: {
		disabled: {
			type: Boolean,
			default: false,
		},
		value: {
			type: String,
			default: null,
		},
		type: {
			type: String as PropType<'datetime' | 'time' | 'date' | 'datetime_created' | 'datetime_updated'>,
			required: true,
			validator: (val: string) =>
				['datetime', 'date', 'time', 'datetime_created', 'datetime_updated'].includes(val),
		},
		includeSeconds: {
			type: Boolean,
			default: false,
		},
	},
	setup(props, { emit }) {
		const formatString = computed(() => {
			const date = 'yyyy-MM-dd';
			const time = 'HH:mm:ss';

			if (props.type === 'datetime') {
				return date + ' ' + time;
			}

			if (props.type === 'date') {
				return date;
			}

			return time;
		});

		const valueAsDate = computed(() => {
			if (props.value === null) return null;

			// The API can return dates as MySQL style (yyyy-mm-dd hh:mm:ss) or ISO 8601.
			// If the value contains a T, it's safe to assume it's a ISO 8601
			if (props.value.includes('T')) {
				return parseISO(props.value);
			}

			return parse(props.value, formatString.value, new Date());
		});

		const displayValue = ref<string>(null);

		syncDisplayValue();

		const localValue = reactive({
			month: null,
			date: null,
			year: null,
			hours: 9,
			minutes: 0,
			seconds: 0,
			period: 'am',
		} as LocalValue);

		syncLocalValue();

		watch(
			() => props.value,
			(newValue, oldValue) => {
				if (newValue !== oldValue && newValue !== null && newValue.length !== 0) {
					syncLocalValue();
					syncDisplayValue();
				}
			}
		);

		watch(
			() => localValue,
			(newValue) => {
				if (
					newValue.year !== null &&
					String(newValue.year).length === 4 &&
					newValue.month !== null &&
					newValue.date !== null &&
					newValue.hours !== null &&
					newValue.minutes !== null &&
					newValue.seconds !== null
				) {
					const { year, month, date, hours, minutes, seconds, period } = newValue;

					const asDate = new Date(year, month, date, period === 'am' ? hours : hours + 12, minutes, seconds);

					emit('input', format(asDate, formatString.value));
				}
			},
			{
				deep: true,
			}
		);

		const { months, dates, years, hours, minutesSeconds } = useOptions();

		return {
			displayValue,
			months,
			dates,
			years,
			hours,
			minutesSeconds,
			setToNow,
			localValue,
			onAMPMInput,
		};

		function setToNow() {
			const date = new Date();

			localValue.month = date.getMonth();
			localValue.date = date.getDate();
			localValue.year = date.getFullYear();
			localValue.hours = date.getHours();
			localValue.minutes = date.getMinutes();
			localValue.seconds = date.getSeconds();
		}

		function syncLocalValue() {
			if (!valueAsDate.value) return;
			localValue.month = valueAsDate.value.getMonth();
			localValue.date = valueAsDate.value.getDate();
			localValue.year = valueAsDate.value?.getFullYear();
			localValue.hours = valueAsDate.value?.getHours() % 12;
			localValue.minutes = valueAsDate.value?.getMinutes();
			localValue.seconds = valueAsDate.value?.getSeconds();
		}

		async function syncDisplayValue() {
			if (valueAsDate.value === null) return null;
			let format = `${i18n.t('date-fns_date')} ${i18n.t('date-fns_time')}`;

			if (props.type === 'date') format = String(i18n.t('date-fns_date'));
			if (props.type === 'time') format = String(i18n.t('date-fns_time'));

			displayValue.value = await formatLocalized(valueAsDate.value as Date, format);
		}

		function onAMPMInput(newValue: 'PM' | 'AM') {
			if (!localValue.hours) return;

			if (newValue === 'AM') {
				localValue.hours = localValue.hours - 12;
			} else {
				localValue.hours = localValue.hours + 12;
			}
		}

		function useOptions() {
			const months = computed(() =>
				[
					i18n.t('months.january'),
					i18n.t('months.february'),
					i18n.t('months.march'),
					i18n.t('months.april'),
					i18n.t('months.may'),
					i18n.t('months.june'),
					i18n.t('months.july'),
					i18n.t('months.august'),
					i18n.t('months.september'),
					i18n.t('months.october'),
					i18n.t('months.november'),
					i18n.t('months.december'),
				].map((text, index) => ({
					text: text,
					value: index,
				}))
			);

			const dates = computed(() => {
				const dates = [];

				for (let i = 1; i <= 31; i++) {
					dates.push(`${i}`);
				}

				return dates;
			});

			const years = computed(() => {
				const current = valueAsDate.value?.getFullYear() || new Date().getFullYear();
				const years = [];
				for (let i = current - 5; i <= current + 5; i++) {
					years.push({
						text: String(i),
						value: i,
					});
				}
				return years;
			});

			const hours = computed(() => {
				const hours = [];

				for (let i = 1; i <= 12; i++) {
					let hour = String(i);
					if (hour.length === 1) hour = '0' + hour;
					hours.push({
						text: hour,
						value: i,
					});
				}

				return hours;
			});

			const minutesSeconds = computed(() => {
				const values = [];

				for (let i = 0; i < 60; i++) {
					let val = String(i);
					if (val.length === 1) val = '0' + val;
					values.push({
						text: val,
						value: i,
					});
				}

				return values;
			});

			return { dates, years, months, hours, minutesSeconds };
		}
	},
});
