















import { defineComponent, computed } from '@vue/composition-api';

export default defineComponent({
	props: {
		value: {
			type: String,
			default: null,
		},
		interfaceOptions: {
			type: Object,
			default: null,
		},
		bold: {
			type: Boolean,
			default: false,
		},
		subdued: {
			type: Boolean,
			default: false,
		},
		font: {
			type: String,
			default: 'sans-serif',
			validator: (val: string) => ['sans-serif', 'serif', 'monospace'].includes(val),
		},
	},
	setup(props) {
		const displayValue = computed(() => props.value.replace(/(<([^>]+)>)/gi, ''));

		return { displayValue };
	},
});
