


































import { defineComponent, computed, ref } from '@vue/composition-api';
import api, { RequestError } from '@/api';
import { translateAPIError } from '@/lang';
import { nanoid } from 'nanoid';

type ProjectInfo = {
	db_host: string | null;
	db_name: string | null;
	db_port: number | null;
	db_user: string | null;
	db_password: string | null;
};

export default defineComponent({
	props: {
		first: {
			type: Boolean,
			default: false,
		},
		database: {
			type: Object,
			required: true,
		},
		project: {
			type: Object,
			required: true,
		},
		token: {
			type: String,
			default: null,
		},
	},
	setup(props) {
		const loading = ref(true);
		const error = ref<RequestError>(null);

		const _token = computed(() => {
			return props.token || nanoid();
		});

		const errorFormatted = computed(() => {
			return error.value && translateAPIError(error.value);
		});

		createProject();

		return { loading, error, errorFormatted, _token };

		async function createProject() {
			try {
				await api.post('/server/projects', {
					...props.database,
					...props.project,
					super_admin_token: _token.value,
				});
			} catch (err) {
				error.value = err;
			} finally {
				loading.value = false;
			}
		}
	},
});
