









































































































import { defineComponent, ref, reactive } from '@vue/composition-api';
import api from '@/api';
import useProjectsStore from '@/stores/projects';
import { Field } from '@/stores/fields/types';
import useCollectionsStore from '@/stores/collections';
import useFieldsStore from '@/stores/fields';
import notify from '@/utils/notify';

export default defineComponent({
	model: {
		prop: 'active',
		event: 'toggle',
	},
	props: {
		active: {
			type: Boolean,
			default: false,
		},
	},
	setup(props, { emit }) {
		const projectsStore = useProjectsStore();
		const collectionsStore = useCollectionsStore();
		const fieldsStore = useFieldsStore();

		const currentTab = ref(['collection']);

		const collectionName = ref(null);
		const primaryKeyFieldName = ref('id');
		const primaryKeyFieldType = ref<'auto_int' | 'uuid' | 'manual'>('auto_int');

		const systemFields = reactive([
			{
				id: 'status',
				enabled: false,
				name: 'status',
				label: 'status',
				icon: 'flag',
			},
			{
				id: 'sort',
				enabled: false,
				name: 'sort',
				label: 'sort',
				icon: 'low_priority',
			},
			{
				id: 'owner',
				enabled: false,
				name: 'created_by',
				label: 'created_by_owner',
				icon: 'account_circle',
			},
			{
				id: 'created_on',
				enabled: false,
				name: 'created_on',
				label: 'created_on',
				icon: 'access_time',
			},
			{
				id: 'modified_by',
				enabled: false,
				name: 'modified_by',
				label: 'modified_by',
				icon: 'account_circle',
			},
			{
				id: 'modified_on',
				enabled: false,
				name: 'modified_on',
				label: 'modified_on',
				icon: 'access_time',
			},
		]);

		const saving = ref(false);
		const saveError = ref(null);

		return {
			currentTab,
			save,
			systemFields,
			primaryKeyFieldName,
			primaryKeyFieldType,
			collectionName,
			saveError,
			saving,
		};

		async function save() {
			const { currentProjectKey } = projectsStore.state;
			saving.value = true;

			try {
				await api.post(`/${currentProjectKey}/collections`, {
					collection: collectionName.value,
					fields: [getPrimaryKeyField(), ...getSystemFields()],
				});

				await collectionsStore.hydrate();
				await fieldsStore.hydrate();

				notify({
					title: 'Collection Created',
					type: 'success',
				});

				emit('toggle', false);
			} catch (error) {
				saveError.value = error;
			} finally {
				saving.value = false;
			}
		}

		function getPrimaryKeyField() {
			const field: Partial<Field> = {
				auto_increment: true,
				field: primaryKeyFieldName.value,
				hidden_browse: false,
				hidden_detail: false,
				interface: 'numeric',
				length: 15,
				primary_key: true,
				type: 'integer',
				datatype: 'INT',
				readonly: true,
			};

			if (primaryKeyFieldType.value === 'uuid') {
				return {
					...field,
					interface: 'text-input',
					type: 'uuid',
					datatype: 'VARCHAR',
					length: 36,
					auto_increment: false,
				};
			} else if (primaryKeyFieldType.value === 'manual') {
				return {
					...field,
					interface: 'text-input',
					type: 'string',
					datatype: 'VARCHAR',
					length: 255,
					auto_increment: false,
					readonly: false,
				};
			} else {
				// auto_int
				return field;
			}
		}

		function getSystemFields() {
			const fields: Partial<Field>[] = [];

			if (systemFields[0].enabled === true) {
				fields.push({
					type: 'status',
					datatype: 'VARCHAR',
					length: 20,
					field: systemFields[0].name,
					interface: 'status',
					default_value: 'draft',
					width: 'full',
					required: true,
					options: {
						status_mapping: {
							published: {
								name: 'Published',
								value: 'published',
								text_color: 'white',
								background_color: 'accent',
								browse_subdued: false,
								browse_badge: true,
								soft_delete: false,
								published: true,
								required_fields: true,
							},
							draft: {
								name: 'Draft',
								value: 'draft',
								text_color: 'white',
								background_color: 'blue-grey-100',
								browse_subdued: true,
								browse_badge: true,
								soft_delete: false,
								published: false,
								required_fields: false,
							},
							deleted: {
								name: 'Deleted',
								value: 'deleted',
								text_color: 'white',
								background_color: 'red',
								browse_subdued: true,
								browse_badge: true,
								soft_delete: true,
								published: false,
								required_fields: false,
							},
						},
					},
				});
			}

			if (systemFields[1].enabled === true) {
				fields.push({
					type: 'sort',
					datatype: 'INT',
					field: systemFields[1].name,
					interface: 'sort',
					hidden_detail: true,
					hidden_browse: true,
					width: 'full',
				});
			}

			if (systemFields[2].enabled === true) {
				fields.push({
					type: 'owner',
					datatype: 'INT',
					field: systemFields[2].name,
					interface: 'owner',
					options: {
						template: '{{first_name}} {{last_name}}',
						display: 'both',
					},
					readonly: true,
					hidden_detail: true,
					hidden_browse: true,
					width: 'full',
				});
			}

			if (systemFields[3].enabled === true) {
				fields.push({
					type: 'datetime_created',
					datatype: 'DATETIME',
					field: systemFields[3].name,
					interface: 'datetime-created',
					readonly: true,
					hidden_detail: true,
					hidden_browse: true,
					width: 'full',
				});
			}

			if (systemFields[4].enabled === true) {
				fields.push({
					type: 'user_updated',
					datatype: 'INT',
					field: systemFields[4].name,
					interface: 'user-updated',
					options: {
						template: '{{first_name}} {{last_name}}',
						display: 'both',
					},
					readonly: true,
					hidden_detail: true,
					hidden_browse: true,
					width: 'full',
				});
			}

			if (systemFields[5].enabled === true) {
				fields.push({
					type: 'datetime_updated',
					datatype: 'DATETIME',
					field: systemFields[5].name,
					interface: 'datetime-updated',
					readonly: true,
					hidden_detail: true,
					hidden_browse: true,
					width: 'full',
				});
			}

			return fields;
		}
	},
});
