










import { defineComponent, computed, PropType } from '@vue/composition-api';

export default defineComponent({
	props: {
		value: {
			type: Array as PropType<string[]>,
			required: true,
		},
	},
	setup(props, { emit }) {
		const _value = computed({
			get() {
				return props.value;
			},
			set(newVal) {
				emit('input', newVal);
			},
		});

		return { _value };
	},
});
