



















































import { defineComponent, ref, watch, computed } from '@vue/composition-api';
import api from '@/api';
import useProjectsStore from '@/stores/projects';
import formatFilesize from '@/utils/format-filesize';
import i18n from '@/lang';
import FileLightbox from '@/views/private/components/file-lightbox';
import ImageEditor from '@/views/private/components/image-editor';
import { nanoid } from 'nanoid';

type Image = {
	type: string;
	data: {
		full_url: string;
		thumbnails: {
			key: string;
			url: string;
		}[];
	};
	filesize: number;
	width: number;
	height: number;
	filename_download: string;
};

export default defineComponent({
	components: { FileLightbox, ImageEditor },
	props: {
		value: {
			type: Number,
			default: null,
		},
		disabled: {
			type: Boolean,
			default: false,
		},
	},
	setup(props, { emit }) {
		const projectsStore = useProjectsStore();

		const loading = ref(false);
		const image = ref<Image>(null);
		const error = ref(null);
		const lightboxActive = ref(false);
		const editorActive = ref(false);

		const cacheBuster = ref(nanoid());

		const src = computed(() => {
			if (!image.value) return null;

			if (image.value.type.includes('svg')) {
				return image.value.data.full_url;
			}

			const url = image.value.data.thumbnails.find((thumb) => thumb.key === 'directus-large-crop')?.url;

			if (url) {
				return `${url}&cache-buster=${cacheBuster.value}`;
			}

			return null;
		});

		const meta = computed(() => {
			if (!image.value) return null;
			const { filesize, width, height, type } = image.value;

			return `${i18n.n(width)}x${i18n.n(height)} • ${formatFilesize(filesize)} • ${type}`;
		});

		watch(
			() => props.value,
			(newID, oldID) => {
				if (newID === oldID) return;

				if (newID) {
					fetchImage();
				}

				if (oldID && newID === null) {
					deselect();
				}
			}
		);

		return {
			loading,
			image,
			error,
			src,
			meta,
			lightboxActive,
			editorActive,
			changeCacheBuster,
			setImage,
			deselect,
		};

		async function fetchImage() {
			const { currentProjectKey } = projectsStore.state;

			loading.value = true;

			try {
				const response = await api.get(`/${currentProjectKey}/files/${props.value}`, {
					params: {
						fields: ['id', 'data', 'title', 'width', 'height', 'filesize', 'type', 'filename_download'],
					},
				});

				image.value = response.data.data;
			} catch (err) {
				error.value = err;
			} finally {
				loading.value = false;
			}
		}

		function changeCacheBuster() {
			cacheBuster.value = nanoid();
		}

		function setImage(data: Image) {
			image.value = data;
		}

		function deselect() {
			emit('input', null);

			loading.value = false;
			image.value = null;
			error.value = null;
			lightboxActive.value = false;
			editorActive.value = false;
		}
	},
});
