































import { defineComponent, PropType, computed } from '@vue/composition-api';
import router from '@/router';

type File = {
	[key: string]: any;
	type: string;
	data: {
		full_url: string;
		thumbnails: {
			key: string;
			url: string;
		}[];
	};
};

export default defineComponent({
	props: {
		icon: {
			type: String,
			default: 'box',
		},
		file: {
			type: Object as PropType<File>,
			default: null,
		},
		crop: {
			type: Boolean,
			default: false,
		},
		loading: {
			type: Boolean,
			default: false,
		},
		item: {
			type: Object as PropType<Record<string, any>>,
			default: null,
		},
		value: {
			type: Array as PropType<(string | number)[]>,
			default: () => [],
		},
		selectMode: {
			type: Boolean,
			default: false,
		},
		to: {
			type: String,
			default: '',
		},
		readonly: {
			type: Boolean,
			default: false,
		},
		itemKey: {
			type: String,
			required: true,
		},
	},
	setup(props, { emit }) {
		const type = computed(() => {
			if (props.file === null) return null;
			if (props.file.type.startsWith('image')) return null;
			return props.file.type.split('/')[1];
		});

		const imageSource = computed(() => {
			if (props.file === null) return null;
			if (props.file.type.startsWith('image') === false) return null;
			if (props.file.type.includes('svg')) return null;

			let key = 'directus-medium-crop';

			if (props.crop === false) {
				key = 'directus-medium-contain';
			}

			const thumbnail = props.file.data.thumbnails.find((thumbnail) => thumbnail.key === key);

			if (!thumbnail) return null;

			return thumbnail.url;
		});

		const svgSource = computed(() => {
			if (props.file === null) return null;
			if (props.file.type.startsWith('image') === false) return null;
			if (props.file.type.includes('svg') === false) return null;

			return props.file.data.full_url;
		});

		const selectionIcon = computed(() => {
			if (!props.item) return 'radio_button_unchecked';

			return props.value.includes(props.item[props.itemKey]) ? 'check_circle' : 'radio_button_unchecked';
		});

		return { imageSource, svgSource, type, selectionIcon, toggleSelection, handleClick };

		function toggleSelection() {
			if (!props.item) return null;

			if (props.value.includes(props.item[props.itemKey])) {
				emit(
					'input',
					props.value.filter((key) => key !== props.item[props.itemKey])
				);
			} else {
				emit('input', [...props.value, props.item[props.itemKey]]);
			}
		}

		function handleClick() {
			if (props.selectMode === true) {
				toggleSelection();
			} else {
				router.push(props.to);
			}
		}
	},
});
