














import { defineComponent, ref, computed } from '@vue/composition-api';
import useProjectsStore from '../../stores/projects';
import api from '@/api';
import { translateAPIError } from '@/lang';
import { RequestError } from '@/api';

export default defineComponent({
	setup() {
		const projectsStore = useProjectsStore();

		const email = ref(null);

		const sending = ref(false);
		const error = ref<RequestError>(null);
		const done = ref(false);

		const errorFormatted = computed(() => {
			if (error.value) {
				return translateAPIError(error.value);
			}
			return null;
		});

		const signInLink = computed(() => `/${projectsStore.state.currentProjectKey}/login`);

		return {
			sending,
			error,
			done,
			email,
			currentProject: projectsStore.currentProject,
			onSubmit,
			signInLink,
			errorFormatted,
		};

		async function onSubmit() {
			sending.value = true;
			error.value = null;

			try {
				await api.post(`/${projectsStore.state.currentProjectKey}/auth/password/request`, {
					email: email.value,
				});

				done.value = true;
			} catch (err) {
				error.value = err;
			} finally {
				sending.value = false;
			}
		}
	},
});
