











































































































import { defineComponent, ref, watch, computed } from '@vue/composition-api';
import ModalBrowse from '@/views/private/components/modal-browse';
import useProjectsStore from '@/stores/projects/';
import api from '@/api';
import readableMimeType from '@/utils/readable-mime-type';

type FileInfo = {
	title: string;
	type: string;
	data: {
		asset_url: string;
		thumbnails?: {
			key: string;
			url: string;
		}[];
	};
};

export default defineComponent({
	components: { ModalBrowse },
	props: {
		value: {
			type: Number,
			default: null,
		},
		disabled: {
			type: Boolean,
			default: false,
		},
	},
	setup(props, { emit }) {
		const projectsStore = useProjectsStore();
		const activeDialog = ref<'upload' | 'choose' | 'url'>(null);
		const { loading, error, file, fetchFile } = useFile();

		watch(() => props.value, fetchFile);

		const fileExtension = computed(() => {
			if (file.value === null) return null;
			return readableMimeType(file.value.type, true);
		});

		const imageThumbnail = computed(() => {
			if (file.value === null) return null;
			if (file.value.type.includes('svg')) return file.value.data.asset_url;
			if (file.value.type.includes('image') === false) return null;
			return file.value.data.thumbnails?.find((thumb) => thumb.key === 'directus-small-crop')?.url;
		});

		const { url, isValidURL, loading: urlLoading, error: urlError, importFromURL } = useURLImport();

		return {
			activeDialog,
			setSelection,
			loading,
			error,
			file,
			fileExtension,
			imageThumbnail,
			onUpload,
			url,
			urlLoading,
			urlError,
			importFromURL,
			isValidURL,
		};

		function useFile() {
			const loading = ref(false);
			const error = ref(null);
			const file = ref<FileInfo>(null);

			return { loading, error, file, fetchFile };

			async function fetchFile() {
				if (props.value === null) {
					file.value = null;
					loading.value = false;
					error.value = null;
					return;
				}

				loading.value = true;
				const { currentProjectKey } = projectsStore.state;

				try {
					const response = await api.get(`/${currentProjectKey}/files/${props.value}`, {
						params: {
							fields: ['title', 'data', 'type', 'filename_download'],
						},
					});

					file.value = response.data.data;
				} catch (err) {
					error.value = err;
				} finally {
					loading.value = false;
				}
			}
		}

		function setSelection(selection: number[]) {
			if (selection[0]) {
				emit('input', selection[0]);
			} else {
				emit('input', null);
			}
		}

		function onUpload(fileInfo: FileInfo) {
			file.value = fileInfo;
			activeDialog.value = null;
		}

		function useURLImport() {
			const url = ref('');
			const loading = ref(false);
			const error = ref(null);

			const isValidURL = computed(() => {
				try {
					new URL(url.value);
					return true;
				} catch {
					return false;
				}
			});

			return { url, loading, error, isValidURL, importFromURL };

			async function importFromURL() {
				loading.value = true;

				const { currentProjectKey } = projectsStore.state;

				try {
					const response = await api.post(`/${currentProjectKey}/files`, {
						data: url.value,
					});

					file.value = response.data.data;

					activeDialog.value = null;
					url.value = '';
				} catch (err) {
					error.value = err;
				} finally {
					loading.value = false;
				}
			}
		}
	},
});
