






import { defineComponent } from '@vue/composition-api';
import { useGroupable } from '@/composables/groupable';

export default defineComponent({
	props: {
		value: {
			type: String,
			default: null,
		},
		scope: {
			type: String,
			default: 'item-group',
		},
	},
	setup(props) {
		const { active, toggle } = useGroupable(props.value, props.scope);
		return { active, toggle };
	},
});
