






import { defineComponent } from '@vue/composition-api';
import useSizeClass, { sizeProps } from '@/composables/size-class';

export default defineComponent({
	props: {
		size: {
			type: Number,
			default: null,
		},
		tile: {
			type: Boolean,
			default: false,
		},
		...sizeProps,
	},
	setup(props) {
		const sizeClass = useSizeClass(props);
		return { sizeClass };
	},
});
